<template>
    <footer class="footer">
      <span class="todo-count">剩余<strong>数量值{{ changdu }}</strong></span>
      <ul class="filters">
        <li>
          <a @click="handler(0)" :class="{selected:num===0}" href="javascript:;" >全部</a>
        </li>
        <li>
          <a @click="handler(1)" :class="{selected:num===1}" href="javascript:;">未完成</a>
        </li>
        <li>
          <a @click="handler(2)" :class="{selected:num===2}" href="javascript:;" >已完成</a>
        </li>
      </ul>
      <button @click="delAll" class="clear-completed" >清除已完成</button>
    </footer>
  </template>
  
  <script>
  export default {
    props:['changdu'],
    data(){
      return {
        num:'',
      }
    },
    methods:{
      handler(key){
        this.num = key
        //传递key值
        this.$emit('filterData',key)
       
      },
      delAll(){
        this.$emit('delAll')
      }
    }
  
  }
  </script>