<template>
    <header class="header">
      <h1>todos</h1>
      <input id="toggle-all" class="toggle-all" type="checkbox" @click="checkAll">
      <label for="toggle-all"></label>
      <input
        @keyup.enter="handler"
        v-model.trim="value"
        class="new-todo"
        placeholder="输入任务名称-回车确认"
        autofocus
      />
    </header>
  </template>
  
  <script>
  export default {
    data(){
        return {
            value:''
        }
    },
    methods:{
        handler(){
            if(this.value ==='') return alert('请输入内容')
            this.$emit('addList',this.value)
            this.value = ''
        },
        checkAll(){
          this.$emit('checkAll',2)
        }
    }
   
  }
  </script>