<template>
  <section class="todoapp">
    <!-- 除了驼峰, 还可以使用-转换链接 -->
    <TodoHeader @addList="handler" @checkAll="checkAll"></TodoHeader>
    <TodoMain :list="dataRest" @dataDel="delHandler"></TodoMain>
    <TodoFooter @filterData="filData" @delAll="delAllData" :changdu="datalength"></TodoFooter>
  </section>
</template>

<script>
// 1.0 样式引入
import "./styles/base.css"
import "./styles/index.css"
    
import TodoHeader from "./components/TodoHeader";
import TodoMain from "./components/TodoMain";
import TodoFooter from "./components/TodoFooter";


export default {
  data() {
    return {
      list: JSON.parse(localStorage.getItem('cunData')) || [
        { id: 100, name: "吃饭", isDone: true },
        { id: 201, name: "睡觉", isDone: false },
        { id: 103, name: "打豆豆", isDone: true },
      ],
      n:'',
    };
  },
  components:{
    TodoHeader,
    TodoMain,
    TodoFooter
  },
  methods:{
    handler(data){
      this.list.push({
        id:Math.random(),
        name:data,
        isDone:false
      })
    },
    delHandler(dataId){
      //获取索引
      let index = this.list.findIndex( item => item.id == dataId)
      //删除数据
      this.list.splice(index,1)
    },
    filData(k){
      this.n = k
    },
    // 清除已完成数据 
    delAllData(){
      this.list = this.list.filter(item => !item.isDone)
    },
    // 全选
    checkAll(){
      this.list.forEach(item => item.isDone=true)
      
    }
  },
  computed:{
    dataRest(){
      
      if(this.n){
        return this.n ===1 ? this.list.filter(item => !item.isDone) : this.list.filter(item => item.isDone)
      } else{
        return this.list
      }
      
    },
    datalength(){
      return this.list.filter(item => !item.isDone).length
    }
  },
  // 本地存储
  watch:{
    list:{
      // immediate:true,
      deep:true,
      handler(){
        localStorage.setItem('cunData',JSON.stringify(this.list))
      }
    }
  }
};
</script>