<template>
    <ul class="todo-list">
      <!-- completed: 完成的类名 -->
      <li v-for=" item in list" :class="{completed:item.isDone}" :key="item.id" :name="item.id">
        <div class="view">
          <input v-model="item.isDone" class="toggle" type="checkbox" />
          <label>{{ item.name }}</label>
          <button @click="del(item.id)" class="destroy"></button>
        </div>
      </li>
    </ul>
    
  </template>
  
  <script>
  export default {
    props:["list"],
    methods:{
        del(id){
            this.$emit('dataDel',id)
        }
    }
  }
  </script>